@import url(https://fonts.googleapis.com/css2?family=Nothing+You+Could+Do&family=Roboto+Condensed:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: "Roboto Condensed", sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.gnav{height:72px;display:flex}.gnav .logo{padding:6px 0}.gnav .logo img{height:60px}.gnav ul{display:flex;list-style:none;padding-left:20px}.gnav ul li{line-height:40px;margin:0 10px}.gnav ul li a{text-decoration:none;color:#333}.gnav ul li a:hover{opacity:.7}
.gfooter{width:600px;max-width:100%;margin:0 auto;text-align:center}.gfooter .btn-group{padding:20px 0;display:flex;flex-wrap:wrap;flex-direction:row-reverse;justify-content:space-around}.gfooter .btn-group a{color:#fff;background:#333;padding:10px 20px;margin-bottom:20px;width:250px;max-width:80%}.gfooter .btn-group a:hover{background:#666}.gfooter .btn-group a:after{content:" >"}.gfooter .btn-group a.back{color:#666;background:#fff;border:2px solid #ccc}.gfooter .btn-group a.back:hover{background:#ccc}.gfooter .btn-group a.back:before{content:"< "}.gfooter .btn-group a.back:after{content:""}.gfooter a{color:#333;display:inline-block;padding:0 5px;text-decoration:none}.gfooter a:hover{opacity:.7}
